<template>
  <div class="info-page">
    <div class="content" v-html="convertedMarkdown"></div>
  </div>
</template>

<script>
import { marked } from 'marked';

export default {
  data() {
    return {
      markdown: `# 

2023-06-30

629

![img](http://www.lfsrd.gov.cn/wp-content/uploads/2023/07/4afeff261720ff81004bfd296f1b65d2.jpg)

 

![img](http://www.lfsrd.gov.cn/wp-content/uploads/2023/07/9c7b17ae3c5dcce011a8a0f2de09fe40.jpg)



 `,
    };
  },
  computed: {
    convertedMarkdown() {
      return marked(this.markdown);
    },
  },
};
</script>

<style>

.info-page{
  display: flex;
  justify-content: center;
  width: 100%;
}
.content{
  width: 50%;
}

</style>



